import React from "react";
import PropTypes from "prop-types";
import clsx from "clsx";
import { withStyles } from "@mui/styles";
import Container from "@mui/material/Container";
import productHeroWonder from "../../../public/static/themes/productHeroWonder.png";
import productHeroArrowDown from "../../../public/static/themes/productHeroArrowDown.png";
import Typography from "../components/Typography";

const styles = (theme) => ({
  root: {
    color: theme.palette.common.white,
    position: "relative",
    display: "flex",
    alignItems: "center",
    [theme.breakpoints.up("sm")]: {
      height: "80vh",
      minHeight: 500,
      maxHeight: 1300,
    },
  },
  container: {
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(14),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  backdrop: {
    position: "absolute",
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    backgroundColor: theme.palette.common.black,
    opacity: 0.5,
    zIndex: -1,
  },
  background: {
    position: "absolute",
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    backgroundSize: "cover",
    backgroundRepeat: "no-repeat",
    zIndex: -2,
  },
  arrowDown: {
    position: "absolute",
    bottom: theme.spacing(4),
  },
});

function ProductHeroLayout(props) {
  const { backgroundClassName, children, classes } = props;

  return (
    <section className={classes.root}>
      <Container className={classes.container} style={{ textAlign: "center" }}>
        <img src={productHeroWonder} alt="wonder" width="147" height="80" />
        {children}
        <div className={classes.backdrop} />
        <div className={clsx(classes.background, backgroundClassName)} />
        <img
          className={classes.arrowDown}
          src={productHeroArrowDown}
          height="16"
          width="12"
          alt="arrow down"
        />
      </Container>
      {/* <Container>
        <Typography variant="h2" gutterBottom>
          Recibe nuestras ofertas
        </Typography>
        <Typography variant="h3" gutterBottom>
          Recibe nuestras ofertas
        </Typography>
        <Typography variant="h4" gutterBottom>
          Recibe nuestras ofertas
        </Typography>
        <Typography variant="h5" gutterBottom>
          Recibe nuestras ofertas
        </Typography>
        <Typography variant="body2" gutterBottom>
          Recibe nuestras ofertas
        </Typography>
        <Typography variant="body1" gutterBottom>
          Recibe nuestras ofertas
        </Typography>
        <Typography variant="button" gutterBottom>
          Recibe nuestras ofertas
        </Typography>
        <Typography variant="subtitle1" gutterBottom>
          Recibe nuestras ofertas
        </Typography>
      </Container> */}
    </section>
  );
}

ProductHeroLayout.propTypes = {
  backgroundClassName: PropTypes.string.isRequired,
  children: PropTypes.node.isRequired,
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(ProductHeroLayout);
